/********************************************* Estilo iOS ***************************************************/
.home-page-slider {
  position: fixed ;
  top: 180;
  right: 25px;
}

.switch {
  visibility: hidden;
  position: absolute;
  margin-left: -9999px;
}

.switch + label {
  display: block;
  position: relative;
  cursor: pointer;
  outline: none;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

/* Estilo Flat */
.switch--flat + label {
  padding: 2px;
  width: 50px;
  height: 27px;
  background-color: #dddddd;
  border-radius: 60px;
  transition: background 0.4s;
}
.switch--flat + label:before,
.switch--flat + label:after {
  display: block;
  position: absolute;
  content: "";
}
.switch--flat + label:before {
  top: 2px;
  left: 2px;
  bottom: 2px;
  right: 2px;
  background-color: #fff;
  border-radius: 60px;
  transition: background 0.4s;
}
.switch--flat + label:after {
  top: 4px;
  left: 4px;
  bottom: 4px;
  width: 20px;
  background-color: #dddddd;
  border-radius: 52px;
  transition: margin 0.4s, background 0.4s;
}
.switch--flat:checked + label {
  background-color: #8ce196;
}
.switch--flat:checked + label:after {
  margin-left: 23px;
  background-color: #8ce196;
}

@media only screen and (max-width: 768px) {
  .home-page-slider {
    position: fixed ;
    top: 115px;
    right: 25px;
  }
}


/* css for cart */

.fixTableHead {
  overflow-y: auto;
  max-height: 80vh;
}
.fixTableHead thead th {
  position: -webkit-sticky;
  position: sticky;
  top: -1px;
  transition: 0.8s ease-in-out;
  background-color: #284561;
}

.cart .img {
  height: 80px;
  width: 80px;
}
.cart .title {
  margin-top: auto;
  margin-bottom: auto;
  margin-left: 20px;
  color: black !important;
}

.cart .card p {
  font-size: 14px;
}

.cart-icon-list {
  border: 1px solid #7e7878;
  border-radius: 4px !important;
  padding: 0 2px;
}

.cart-icon-list:hover{
  background-color: rgba(125, 176, 223, 0.563);
  transition: 0.4s;
}

.plus-minus-icon {
  color: black !important;
}

@media only screen and (max-width: 768px) {
  .fixTableHead {
    font-size: 12px;
  }
}



